.about{
  .top();
}
// h2 title
.about-heading{
  .heading();
}
.about-wrapp{
  display: flex;
  justify-content: space-between;
}
// h3 title
.background-title,
.merits-title,
.connection-title{
  .title();
}
// block-left
.about-item__background{
  .block();
}
.background-text{
  text-transform: none;
  color: #22241b;
  line-height: 1.8;
  padding-bottom: 25px;
}
// block-right
.about-block{
  .block();
}

// merits
.about-item__merits{
  padding-bottom: 65px;
}
.merits-items{
  display: flex;
}
.merits-item{
  display: flex;
  flex-direction: column;
  flex-basis: 50%;
  margin-right: 30px;
}

// rhombus
.merits-item__value{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'MuseoSansCyrl 100';
  font-size: 20px;
  color: #214c17;
  padding-top: 55px;
  padding-bottom: 70px;
}
.merits-item__value:after {
  content: '';
  position: absolute;
  width: 80px;
  height: 80px;
  border: 2px solid #cedac2;
  outline: 2px solid #3f6534;
  outline-offset: 5px;
  transform: rotate(45deg);
}
.merits-item__text{
  display: block;
  text-align: center;
  text-transform: uppercase;
  font-family: 'MuseoSansCyrl 100';
  font-size: 12px;
}


// connection
.connection-items{
  display: flex;
  align-items: center;
}
.connection-item{
  text-transform: none;
  font-size: 14px;
  color: #204d15;
}
.connection-item__icon{
  display: flex;
  align-items: center;
}
// item-list
.item-social__list{
  margin-right: 40px;
}
.item-social__list-item{
  padding-bottom: 16px;
}
.list-item__link{
  .line-bottom();
}
.icon-back{
  padding: 18px 18px 18px 20px;
  background: #eff1ee;
  border-radius: 50%;
  margin-right: 20px;
}
.icon-back:hover{
  cursor: pointer;
}



// media
@media screen and (max-width: 900px){

  .merits-item{
    margin-right: 0;
  }
  .merits-item__value:after{
    width: 70px;
    height: 70px;
  }
}

@media screen and (max-width: 820px){

  .about,
  .skills,
  .portfolio{
    padding-top: 60px;
  }
  .about-heading,
  .skills-heading,
  .portfolio-heading{
    font-size: 32px;
    padding-bottom: 65px;
  }
  .about-item__background,
  .about-block{
    padding-right: 15px;
  }
  .background-title,
  .merits-title,
  .connection-title{
    font-size: 18px;
    padding-bottom: 25px;
  }
  .background-text{
    line-height: 1.6;
    padding-bottom: 20px;
  }
  .about-item__merits{
    padding-bottom: 50px;
  }
  .merits-item__value{
    font-size: 18px;
    padding-top: 55px;
    padding-bottom: 70px;
  }
  .item-social__list{
    margin-right: 30px;
  }
}

@media screen and (max-width: 800px){

  .merits-title,
  .connection-title{
    padding-bottom: 10px;
  }
  .merits-items{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .merits-item__value{
    font-size: 16px;
    padding-top: 45px;
    padding-bottom: 50px;
  }
  .merits-item__value:after{
    width: 60px;
    height: 60px;
  }
  .about-item__merits {
    padding-bottom: 30px;
  }
  .connection-items{
    flex-direction: column;
    justify-content: center;
  }
}

@media screen and (max-width: 600px){

  .about,
  .skills,
  .portfolio{
    padding-top: 40px;
  }
  .about-heading,
  .skills-heading,
  .portfolio-heading{
    font-size: 28px;
    padding-bottom: 45px;
  }
  .about-item__background,
  .about-block{
    padding-right: 15px;
  }
  .background-title,
  .merits-title,
  .connection-title{
    font-size: 16px;
    padding-bottom: 10px;
  }
  .background-text{
    line-height: 1.2;
    padding-bottom: 10px;
  }
  .about-item__merits{
    padding-bottom: 20px;
  }
  .merits-item__value{
    font-size: 14px;
    padding-top: 45px;
    padding-bottom: 50px;
  }
  .merits-item__value:after{
    width: 50px;
    height: 50px;
  }
  .merits-item__text{
    font-size: 10px;
  }
  .connection-item{
    font-size: 12px;
  }
  .item-social__list{
    margin-right: 10px;
  }
  .item-social__list-item{
    padding-bottom: 10px;
  }
  .list-item__link{
    padding-bottom: 2px;
  }
  // icon
  .icon-back{
    padding: 15px 15px 15px 17px;
    margin-right: 12px;
  }
  .icon-img{
    height: 22px;
    width: 16px;
  }
}

@media screen and (max-width: 480px){

  .about,
  .skills,
  .portfolio{
    padding-top: 25px;
  }
  .about-heading,
  .skills-heading,
  .portfolio-heading{
    font-size: 22px;
    padding-bottom: 20px;
  }
  .about-wrapp{
    display: flex;
    flex-direction: column;
  }
  .about-item__background,
  .about-block{
    max-width: none;
  }
  .about-item__background{
    padding-bottom: 15px;
  }
  .connection-item__icon{
    display: none;
  }
}