.header-top,
.header-title,
.header-footer,
.about,
.skills,
.portfolio,
.footer{
  max-width: 980px;
  margin: 0 auto;
  padding: 0 20px;
}
      /* container */
.header-top,
.header-title{
  position: relative;
}

.header-top::after,
.header-title::after{
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}