html {
  box-sizing: border-box;
  overflow-x: hidden;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  font-family: 'MuseoSansCyrl 300';
  font-size: 15px;
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
p,
ul,
li,
figure,
input,
button {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  font-style: normal;
  color: inherit;
}
input:active,
input:focus,
input:hover {
  outline: 0;
  outline-offset: 0;
}
button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  /* inherit font & color from ancestor */
  color: inherit;
  font: inherit;
  /* Normalize `line-height`. Cannot be changed from `normal` in Firefox 4+. */
  line-height: normal;
  /* Corrects font smoothing for webkit */
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  /* Corrects inability to style clickable `input` types in iOS */
  -webkit-appearance: none;
}
button:active,
button:focus,
button:hover {
  outline: 0;
  outline-offset: 0;
}
.icon {
  fill: #fff;
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  font-size: 25px;
}
.icon-img {
  fill: #204d15;
  height: 26px;
  width: 20px;
}
.header-top,
.header-title,
.header-footer,
.about,
.skills,
.portfolio,
.footer {
  max-width: 980px;
  margin: 0 auto;
  padding: 0 20px;
}
/* container */
.header-top,
.header-title {
  position: relative;
}
.header-top::after,
.header-title::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}
/* flex-box */
.header {
  text-transform: uppercase;
}
.header-back {
  position: absolute;
  min-height: 100vh;
}
.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'MuseoSansCyrl 900';
  font-size: 17px;
  color: #fff;
  padding-top: 52px;
}
.logo-text {
  font-family: 'MuseoSansCyrl 100';
}
.burger-btn {
  display: none;
  padding: 10px 15px;
}
.burger-btn:hover {
  cursor: pointer;
}
.burger-btn:hover .burger {
  fill: #686966;
}
.menu-list {
  display: flex;
}
.menu-list__item:not(:last-child) {
  margin-right: 48px;
}
.menu-item__link:hover,
.menu-item__link:active {
  color: #686966;
}
.header-top__mail {
  font-family: 'MuseoSansCyrl 300';
  font-size: 13px;
  text-transform: none;
}
.header-top__mail .mail {
  color: #fff;
  padding-bottom: 4px;
  border-bottom: 1px solid #686966;
}
.mail:hover {
  color: #686966;
  border-color: #fff;
}
.header-title {
  padding-top: 280px;
  padding-bottom: 255px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
  letter-spacing: 5px;
}
.header-title__heading {
  font-family: 'Natasha';
  font-size: 49px;
}
.heading-text {
  padding-top: 25px;
  font-family: 'MuseoSansCyrl 100';
  font-size: 29px;
}
.header-footer {
  display: flex;
  justify-content: center;
  position: relative;
  padding-bottom: 75px;
}
.header-footer__link {
  padding: 11px;
  border: 1px solid #fff;
  border-radius: 50%;
}
.header-footer__link:hover {
  cursor: pointer;
  background: #fff;
}
.header-footer__link:hover .icon {
  fill: #686966;
}
.connection-item {
  text-transform: none;
}
@media screen and (max-width: 780px) {
  .header-back {
    min-height: 0;
    height: 100%;
  }
  .header-title {
    padding: 200px 0;
    letter-spacing: 4px;
  }
  .header-title__heading {
    font-size: 40px;
  }
  .heading-text {
    padding-top: 20px;
    font-size: 22px;
  }
  .header-footer {
    padding-bottom: 50px;
  }
  .header-footer__link {
    padding: 9px;
  }
}
@media screen and (max-width: 680px) {
  .header-back {
    height: 90%;
  }
  .header-top {
    justify-content: normal;
    flex-direction: column;
    font-size: 16px;
    padding-top: 48px;
  }
  .header-top__menu {
    padding: 20px 0;
  }
  .header-title {
    padding: 160px 0 210px 0;
    letter-spacing: 3px;
  }
  .header-title__heading {
    font-size: 38px;
  }
  .heading-text {
    padding-top: 18px;
    font-size: 20px;
  }
  .header-footer {
    display: none;
  }
}
@media screen and (max-width: 580px) {
  .header-back {
    height: 73%;
  }
  .header-top {
    font-size: 16px;
    padding-top: 30px;
  }
  .header-top__menu {
    padding: 16px 0;
  }
  .header-title {
    padding: 100px 0 200px 0;
    letter-spacing: 3px;
  }
  .header-title__heading {
    font-size: 32px;
  }
  .heading-text {
    padding-top: 14px;
    font-size: 18px;
  }
}
@media screen and (max-width: 480px) {
  .header-back {
    height: 62%;
  }
  .header-top {
    font-size: 14px;
    padding-top: 20px;
  }
  .header-top__menu {
    padding: 14px 0;
  }
  .header-title {
    padding: 80px 0 180px 0;
    letter-spacing: 2px;
  }
  .header-title__heading {
    font-size: 24px;
  }
  .heading-text {
    padding-top: 10px;
    font-size: 15px;
  }
}
@media screen and (max-width: 380px) {
  .header-back {
    height: 51%;
  }
  .header-top {
    position: relative;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    font-size: 12px;
    padding-top: 18px;
  }
  .header-title {
    padding: 80px 0 150px 0;
  }
  .burger-btn {
    display: block;
  }
  .header-top__menu {
    position: absolute;
    top: 50%;
    left: 53%;
  }
  .header-top__menu .menu-list {
    flex-direction: column;
  }
  .header-top__menu .menu-list__item {
    padding: 8px 0;
  }
  .header-top__mail {
    display: none;
  }
  .header-title__heading {
    font-size: 18px;
  }
  .heading-text {
    padding-top: 10px;
    font-size: 13px;
  }
}
.about {
  text-transform: uppercase;
  padding-top: 85px;
}
.about-heading {
  font-family: 'Natasha';
  font-size: 37px;
  color: #22241b;
  padding-bottom: 78px;
}
.about-wrapp {
  display: flex;
  justify-content: space-between;
}
.background-title,
.merits-title,
.connection-title {
  font-family: 'Natasha';
  font-size: 22px;
  color: rgba(34, 36, 27, 0.659);
  padding-bottom: 35px;
}
.about-item__background {
  display: flex;
  flex-direction: column;
  max-width: 50%;
  padding-right: 20px;
}
.background-text {
  text-transform: none;
  color: #22241b;
  line-height: 1.8;
  padding-bottom: 25px;
}
.about-block {
  display: flex;
  flex-direction: column;
  max-width: 50%;
  padding-right: 20px;
}
.about-item__merits {
  padding-bottom: 65px;
}
.merits-items {
  display: flex;
}
.merits-item {
  display: flex;
  flex-direction: column;
  flex-basis: 50%;
  margin-right: 30px;
}
.merits-item__value {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'MuseoSansCyrl 100';
  font-size: 20px;
  color: #214c17;
  padding-top: 55px;
  padding-bottom: 70px;
}
.merits-item__value:after {
  content: '';
  position: absolute;
  width: 80px;
  height: 80px;
  border: 2px solid #cedac2;
  outline: 2px solid #3f6534;
  outline-offset: 5px;
  transform: rotate(45deg);
}
.merits-item__text {
  display: block;
  text-align: center;
  text-transform: uppercase;
  font-family: 'MuseoSansCyrl 100';
  font-size: 12px;
}
.connection-items {
  display: flex;
  align-items: center;
}
.connection-item {
  text-transform: none;
  font-size: 14px;
  color: #204d15;
}
.connection-item__icon {
  display: flex;
  align-items: center;
}
.item-social__list {
  margin-right: 40px;
}
.item-social__list-item {
  padding-bottom: 16px;
}
.list-item__link {
  padding-bottom: 4px;
  border-bottom: 1px solid #686966;
}
.icon-back {
  padding: 18px 18px 18px 20px;
  background: #eff1ee;
  border-radius: 50%;
  margin-right: 20px;
}
.icon-back:hover {
  cursor: pointer;
}
@media screen and (max-width: 900px) {
  .merits-item {
    margin-right: 0;
  }
  .merits-item__value:after {
    width: 70px;
    height: 70px;
  }
}
@media screen and (max-width: 820px) {
  .about,
  .skills,
  .portfolio {
    padding-top: 60px;
  }
  .about-heading,
  .skills-heading,
  .portfolio-heading {
    font-size: 32px;
    padding-bottom: 65px;
  }
  .about-item__background,
  .about-block {
    padding-right: 15px;
  }
  .background-title,
  .merits-title,
  .connection-title {
    font-size: 18px;
    padding-bottom: 25px;
  }
  .background-text {
    line-height: 1.6;
    padding-bottom: 20px;
  }
  .about-item__merits {
    padding-bottom: 50px;
  }
  .merits-item__value {
    font-size: 18px;
    padding-top: 55px;
    padding-bottom: 70px;
  }
  .item-social__list {
    margin-right: 30px;
  }
}
@media screen and (max-width: 800px) {
  .merits-title,
  .connection-title {
    padding-bottom: 10px;
  }
  .merits-items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .merits-item__value {
    font-size: 16px;
    padding-top: 45px;
    padding-bottom: 50px;
  }
  .merits-item__value:after {
    width: 60px;
    height: 60px;
  }
  .about-item__merits {
    padding-bottom: 30px;
  }
  .connection-items {
    flex-direction: column;
    justify-content: center;
  }
}
@media screen and (max-width: 600px) {
  .about,
  .skills,
  .portfolio {
    padding-top: 40px;
  }
  .about-heading,
  .skills-heading,
  .portfolio-heading {
    font-size: 28px;
    padding-bottom: 45px;
  }
  .about-item__background,
  .about-block {
    padding-right: 15px;
  }
  .background-title,
  .merits-title,
  .connection-title {
    font-size: 16px;
    padding-bottom: 10px;
  }
  .background-text {
    line-height: 1.2;
    padding-bottom: 10px;
  }
  .about-item__merits {
    padding-bottom: 20px;
  }
  .merits-item__value {
    font-size: 14px;
    padding-top: 45px;
    padding-bottom: 50px;
  }
  .merits-item__value:after {
    width: 50px;
    height: 50px;
  }
  .merits-item__text {
    font-size: 10px;
  }
  .connection-item {
    font-size: 12px;
  }
  .item-social__list {
    margin-right: 10px;
  }
  .item-social__list-item {
    padding-bottom: 10px;
  }
  .list-item__link {
    padding-bottom: 2px;
  }
  .icon-back {
    padding: 15px 15px 15px 17px;
    margin-right: 12px;
  }
  .icon-img {
    height: 22px;
    width: 16px;
  }
}
@media screen and (max-width: 480px) {
  .about,
  .skills,
  .portfolio {
    padding-top: 25px;
  }
  .about-heading,
  .skills-heading,
  .portfolio-heading {
    font-size: 22px;
    padding-bottom: 20px;
  }
  .about-wrapp {
    display: flex;
    flex-direction: column;
  }
  .about-item__background,
  .about-block {
    max-width: none;
  }
  .about-item__background {
    padding-bottom: 15px;
  }
  .connection-item__icon {
    display: none;
  }
}
.skills {
  text-transform: uppercase;
  padding-top: 85px;
}
.skills-heading {
  font-family: 'Natasha';
  font-size: 37px;
  color: #22241b;
  padding-bottom: 78px;
}
.skills-items {
  text-transform: uppercase;
  display: flex;
  justify-content: space-between;
}
.skills-item {
  display: flex;
  align-items: center;
}
.skills-item__value {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'MuseoSansCyrl 100';
  font-size: 13px;
  color: #204d16;
  width: 62px;
  height: 62px;
  border-radius: 50%;
  border: 1px solid #204d14;
  margin-right: 27px;
}
.skills-item__value::after {
  position: absolute;
  content: '';
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 1px solid #adb2ac;
  margin: 5px;
  bottom: 0.5px;
  left: 0;
}
@media screen and (max-width: 700px) {
  .skills-items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .skills-item {
    padding-bottom: 20px;
  }
  .skills-item__value {
    margin-right: 18px;
  }
  .skills-item__text {
    margin-right: 15px;
  }
}
@media screen and (max-width: 400px) {
  .skills-items {
    flex-direction: column;
    padding-left: 30px;
  }
}
.portfolio {
  text-transform: uppercase;
  padding-top: 85px;
}
.portfolio-heading {
  font-family: 'Natasha';
  font-size: 37px;
  color: #22241b;
  padding-bottom: 78px;
  padding-bottom: 68px;
}
.portfolio-items {
  display: flex;
}
.portfolio-image {
  position: relative;
  box-sizing: border-box;
  margin: 10px;
  width: calc(100% / 3 - 20px);
}
.portfolio-image:hover {
  cursor: pointer;
}
.item-image {
  width: 100%;
  height: 100%;
}
.item-hover {
  position: absolute;
  content: '';
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
}
.clear {
  display: none;
}
.item-hover__text {
  text-transform: uppercase;
  font-family: 'MuseoSansCyrl 900';
  font-size: 15px;
  color: #fff;
  padding-bottom: 13px;
}
.item-hover__link {
  font-size: 13px;
  color: #fff;
  padding-bottom: 4px;
  border-bottom: 1px solid #686966;
}
@media screen and (max-width: 800px) {
  .portfolio-items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .portfolio-image {
    width: calc(100%/2 - 20px);
  }
}
@media screen and (max-width: 500px) {
  .portfolio-image {
    width: calc(100%/1 - 20px);
  }
}
.footer {
  padding-top: 115px;
  padding-bottom: 30px;
  text-transform: uppercase;
  font-size: 11px;
  color: #898987;
}
