.header{
  text-transform: uppercase;;
}
.header-back{
  position: absolute;
  min-height: 100vh;
}
.header-top{
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'MuseoSansCyrl 900';
  font-size: 17px;
  color: #fff;
  padding-top: 52px;
}
// logo
.logo-text{
   font-family: 'MuseoSansCyrl 100';
 }
// burger

.burger-btn{
  display: none;
  padding: 10px 15px;
}
.burger-btn:hover{
  cursor: pointer;
  .burger{
    fill: #686966;
  }
}
// nav menu
.menu-list{
  display: flex;
}
.menu-list__item:not(:last-child){
  margin-right: 48px;
}
.menu-item__link:hover,
.menu-item__link:active{
  color: #686966;
}
// mail
.header-top__mail{
  font-family: 'MuseoSansCyrl 300';
  font-size: 13px;
  text-transform: none;
  .mail{
    color: #fff;
    .line-bottom();
  }
}
.mail:hover{
  color: #686966;
  border-color: #fff;
}
// heading
.header-title{
  padding-top: 280px;
  padding-bottom: 255px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
  letter-spacing: 5px;
}
.header-title__heading{
  font-family: 'Natasha';
  font-size: 49px;
}
.heading-text{
  padding-top: 25px;
  font-family: 'MuseoSansCyrl 100';
  font-size: 29px;
}
// icon
.header-footer{
  display: flex;
  justify-content: center;
  position: relative;
  padding-bottom: 75px;
}
.header-footer__link{
  padding: 11px;
  border: 1px solid #fff;
  border-radius: 50%;
}
.header-footer__link:hover{
  cursor: pointer;
  background: #fff;
  .icon{
    fill: #686966;
  }
}
// connection
.connection-item{
  text-transform: none;
}



// media
@media screen and (max-width: 780px){

  .header-back{
    min-height: 0;
    height: 100%;
  }
  .header-title{
    padding: 200px 0;
    letter-spacing: 4px;
  }
  .header-title__heading{
    font-size: 40px;
  }
  .heading-text{
    padding-top: 20px;
    font-size: 22px;
  }
  .header-footer{
    padding-bottom: 50px;
  }
  .header-footer__link{
    padding: 9px;
  }
}

@media screen and (max-width: 680px){

  .header-back{
    height: 90%;
  }
  .header-top{
    justify-content: normal;
    flex-direction: column;
    font-size: 16px;
    padding-top: 48px;
  }
  .header-top__menu{
    padding: 20px 0;
  }
  .header-title{
    padding: 160px 0 210px 0;
    letter-spacing: 3px;
  }
  .header-title__heading{
    font-size: 38px;
  }
  .heading-text{
    padding-top: 18px;
    font-size: 20px;
  }
  .header-footer{
    display: none;
  }
}

@media screen and (max-width: 580px){

  .header-back{
    height: 73%;
  }
  .header-top{
    font-size: 16px;
    padding-top: 30px;
  }
  .header-top__menu{
    padding: 16px 0;
  }
  .header-title{
    padding: 100px 0 200px 0;
    letter-spacing: 3px;
  }
  .header-title__heading{
    font-size: 32px;
  }
  .heading-text{
    padding-top: 14px;
    font-size: 18px;
  }
}

@media screen and (max-width: 480px){

  .header-back{
    height: 62%;
  }
  .header-top{
    font-size: 14px;
    padding-top: 20px;
  }
  .header-top__menu{
    padding: 14px 0;
  }
  .header-title{
    padding: 80px 0 180px 0;
    letter-spacing: 2px;
  }
  .header-title__heading{
    font-size: 24px;
  }
  .heading-text{
    padding-top: 10px;
    font-size: 15px;
  }
}

@media screen and (max-width: 380px){

  .header-back{
    height: 51%;
  }
  .header-top{
    position: relative;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    font-size: 12px;
    padding-top: 18px;
  }
  .header-title {
    padding: 80px 0 150px 0;
  }
  .burger-btn{
    display: block;
  }
  .header-top__menu{
    position: absolute;
    top: 50%;
    left: 53%;

    .menu-list{
      flex-direction: column;
    }
    .menu-list__item{
      padding: 8px 0;
    }
  }
  .header-top__mail{
    display: none;
  }
  .header-title__heading{
    font-size: 18px;
  }
  .heading-text{
    padding-top: 10px;
    font-size: 13px;
  }
}