.skills{
  .top();
}
.skills-heading{
  .heading();
}
.skills-items{
  text-transform: uppercase;
  display: flex;
  justify-content: space-between;
}
.skills-item{
  display: flex;
  align-items: center;
}

// circles
.skills-item__value{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'MuseoSansCyrl 100';
  font-size: 13px;
  color: #204d16;
  width: 62px;
  height: 62px;
  border-radius: 50%;
  border: 1px solid #204d14;
  margin-right: 27px;
}
.skills-item__value::after{
  position: absolute;
  content: '';
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 1px solid #adb2ac;
  margin: 5px;
  bottom: .5px;
  left: 0;
}

// media
@media screen and (max-width: 700px){

  .skills-items{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .skills-item{
    padding-bottom: 20px;
  }
  .skills-item__value{
    margin-right: 18px;
  }
  .skills-item__text{
    margin-right: 15px;
  }
}

@media screen and (max-width: 400px){

  .skills-items{
    flex-direction: column;
    padding-left: 30px;
  }
}