.portfolio{
  .top();
}
.portfolio-heading{
  .heading();
  padding-bottom: 68px;
}
.portfolio-items{
  display: flex;
}
.portfolio-image{
  position: relative;
  .col();
  .size(3);
}
.portfolio-image:hover{
  cursor: pointer;
}
.item-image{
  width: 100%;
  height: 100%;
}
// hovers
.item-hover {
  position: absolute;
  content: '';
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,.4)
}
.clear{
  display: none;
}
.item-hover__text{
  text-transform: uppercase;
  font-family: 'MuseoSansCyrl 900';
  font-size: 15px;
  color: #fff;
  padding-bottom: 13px;
}
.item-hover__link{
  font-size: 13px;
  color: #fff;
  .line-bottom();
}



// media
@media screen and (max-width: 800px){

  .portfolio-items{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .portfolio-image{
    width: calc(100%/2 - 20px);
  }
}

@media screen and (max-width: 500px){

  .portfolio-image{
    width: calc(100%/1 - 20px);
  }
}